









import { Vue, Component, Prop, Provide, Emit, Inject, Mixins } from 'vue-property-decorator'
import { CreateElement, VNode, VNodeData } from 'vue'
// @ts-ignore
import ProjectTreeItem from './tree-item.vue'
import { State, Mutation, Getter } from 'vuex-class'
import { UiNodes, StatePreviewer } from '@/store'
import { UiNode } from '@megh5/ui/types/core/constants'

@Component({
  components: { ProjectTreeItem }
})
export default class Tree extends Vue {
  @Getter UiNodes!: UiNodes

  @State Previewer!: StatePreviewer

  RItem (nodes: UiNode[]) {
    const result: VNode[] = []

    for (let node of nodes) {
      result.push(
        <ProjectTreeItem value={node} />
      )
      if (node.children) {
        result.push(
          ...this.RItem(node.children)
        )
      }
    }

    return result
  }

  render () {
    const { UiNodes } = this

    return (
      <div class="tree">
        <div class="tree-scroller">
          {this.Previewer.ready ?
            this.RItem(UiNodes) :
            undefined
          }
        </div>
      </div>
    )
  }
}
